@import url(https://fonts.googleapis.com/css2?family=Aldrich&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	
	margin: 0;
	padding: 0;
	border: 0;
	font: inherit;
	font-size: 100%;	
	vertical-align: baseline;
	box-sizing: border-box;
	
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
	
}
a{
	text-decoration: none;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

#root{
    width: 100vw;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

:root{

    /* color palette */

    --darker-white : #b9bbbe;
    --dark-white : #eaeaeb;
    --light-white : #f5f5f5;

    
    --dark-black: #2f3136;
    --light-black: #4f545c;
    --lighter-black: #72767d;

    --darker-violet: #322a54;
    --dark-violet : #36303f;
    --violet : #5d5381;
    --light-violet : #754bce;
    
    --profile-card-aspect-ratio: 0.55238095238095238095238095238095;
    --profile-card-height: 60vh;
}

.Layout_mainContainer__25ZmF{
    /* background-color: #1a1c20; */
    position: relative;
    background: linear-gradient(328deg, rgba(0,0,0,1) 0%, #1a1c20 70% , rgba(55,58,64,1) 100%);
    display: grid;
    height: 100%;
    width: 100%;
    grid-template-columns: 
        minmax(200px, calc( 60vh * 0.55238095238095238095238095238095))
        1fr 
        10vw;
    grid-template-columns: 
        minmax(200px, calc( var(--profile-card-height) * var(--profile-card-aspect-ratio)))
        1fr /*content*/
        10vw; /*navigation reader*/
    grid-template-rows: 
        7vh  /*header height*/
        1fr;
    grid-gap: 3rem 3rem;
    gap: 3rem 3rem;
    padding: 0 6rem;
}

