.ProfileCard_localScope__2FKvr{
    --image-aspect-ratio: 1.7777777777777777777777777777778;
}

.ProfileCard_card__3MYxz{
    z-index: 8;
    grid-column: 1 / span 1;
    grid-row: 1 / -1;
    margin: auto 0;
    width: 100%;
    height: var(--profile-card-height);
    background-color: var(--violet);
    box-shadow: 8px 8px 10px 4px rgba(0, 0, 0, .7);
    border-radius: 4px;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-flow: column nowrap;
}

.ProfileCard_card__3MYxz::after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--light-violet);
    transform: translateX(50%) skew(-5deg);
    z-index: -1;
}

.ProfileCard_cardContent__1g46Q{
    width: 100%;
    height: 80%;
    flex-grow: 1;
    flex-shrink: 1;
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-between;
}



.ProfileCard_image__2k5k8{
    display: block;
    max-width: 200px;
    height: 200px;
    margin: 1.5rem auto;
    object-fit: cover;
    object-position: center;
    border-radius: 50%;    
}

.ProfileCard_intro__14ZP3{

}

.ProfileCard_name__3Bb9M{
    font-family: 'Aldrich', sans-serif;
    font-size: 32px;
    text-align: center;
    margin-bottom: 1rem;
    color: var(--light-white)
}

.ProfileCard_jobTitle__1O6r8{
    font-family: 'Aldrich', sans-serif;
    font-size: 28px;
    color: var(--dark-white);
    text-align: center;

}

.ProfileCard_contactIcons__2iLCV{
    /* margin-top: 1.5em; */
    display: flex;
    justify-content: center;
    color: var(--darker-white);
    font-size: 32px;
    font-family: 'Roboto', sans-serif;
}
.ProfileCard_iconContainer__1aGrG{
    position: relative;
    cursor: pointer;
    line-height: 1rem;
    padding: .3em;
    margin: 0 .1em;
    border: 2px solid var(--dark-black);
    border-radius: 50%;
    background-color: black;
}


.ProfileCard_iconContainer__1aGrG::after{
    content: attr(data-url);
    font-size: 18px;
    padding: .4em;
    white-space: nowrap;
    border-radius: 8px;
    background-color: var(--darker-violet);
    position: absolute;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    color: white;
    visibility: hidden;
    transition: all .2s;
}
.ProfileCard_iconContainer__1aGrG::before{
    content:'';
    position: absolute;
    top: 0;
    left: 50%;
    background-color: var(--darker-violet);
    width: 10px;
    height: 10px;
    transform: rotate(45deg) translateX(-50%) scale(0);
    color: white;
    visibility: hidden;
    transition: all .2s;
}

.ProfileCard_iconContainer__1aGrG:hover::after{
    visibility: visible;
    top: -50%;
    transform: translate(-50%, -50%) scale(1);
}
.ProfileCard_iconContainer__1aGrG:hover::before
{
    visibility: visible;
    top: -.4em;
    transform: rotate(45deg) translateX(-50%) scale(1);
}
.ProfileCard_icon__5n5gB{
    transition: .3s;
}

.ProfileCard_cardFooter__3qLme{
    height: 20%;
    flex-grow: 0;
    flex-shrink: 0;
    display: flex;
    /* align-content: center; */
}

.ProfileCard_downloadButton__3lIJW{
    margin: auto;
